import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllLeaveRequestsComponent } from './leave-requests/all-leave-requests/all-leave-requests.component';
import { AllLeavePlannersComponent } from './leave-planner/all-leave-planners/all-leave-planners.component';
import { LeaveReportsComponent } from './leave-reports/leave-reports.component';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';

const routes: Routes = [
  {
    path: "all-leave-requests",
    component: AllLeaveRequestsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },

  {
    path: "all-leave-planners",
    component: AllLeavePlannersComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },

  {
    path: "leave-reports",
    component: LeaveReportsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LeaveManagementRoutingModule { }
