import { SelectionModel } from "@angular/cdk/collections";
import { Component, OnInit, ViewChild } from "@angular/core";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Subject, takeUntil, Subscription } from "rxjs";
import { NotificationServiceService } from "src/@core/Models/Notification/notification-service.service";
import { HolidayConfigurationService } from "src/app/erp-hr/data/configuration-services/holiday-configuration.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import Swal from "sweetalert2";
import { ManageLeaveRequestsComponent } from "../manage-leave-requests/manage-leave-requests.component";
import { LeaveRequestService } from "src/app/erp-hr/data/leave-services/leave-requests.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { TokenCookieService } from "src/app/core/service/token-storage-cookies.service";
import { TokenStorageService } from "src/app/core/service/token-storage.service";
import { CcentreLookupComponent } from "src/app/erp-finance/data/lookups/ccentre-lookup/ccentre-lookup.component";

@Component({
  selector: "app-all-leave-requests",
  templateUrl: "./all-leave-requests.component.html",
  styleUrls: ["./all-leave-requests.component.scss"],
})
export class AllLeaveRequestsComponent implements OnInit {
  displayedColumns: string[] = [
    "select",
    "id",
    "empPf",
    "fromDate",
    "handoverToEmpPf",
    "leaveReason",
    "fromDate",
    "toDate",
    "supervisorstatus",
    "hrstatus",
    "status",
    "action",
  ];

  loading: boolean = false;
  data: any;
  error: any;
  dialogConfig: any;
  destroy$: Subject<boolean> = new Subject<boolean>();

  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  selection = new SelectionModel<any>(true, []);

  downloadLoading: boolean = false;
  posting: boolean = false;
  showForm: boolean = false;

  Form: FormGroup;
  months: string[] = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ];

  currentUser: any;

  constructor(
    private dialog: MatDialog,
    private notificationAPI: NotificationServiceService,
    private leaveRequestService: LeaveRequestService,
    private snackbar: SnackbarService,
    private fb: FormBuilder,
    private tokenStorageService: TokenStorageService
  ) { }

  ngOnInit(): void {
    const currentYear = new Date().getFullYear();
    const currentMonthIndex = new Date().getMonth();
    const currentMonthName = this.months[currentMonthIndex];

    this.currentUser = this.tokenStorageService.getUser();

    this.Form = this.fb.group({
      costCenterCode: [this.currentUser.costCenterCode, Validators.required],
      year: [currentYear.toString(), Validators.required],
      status: ["ALL"],
    });
    this.Form.valueChanges.subscribe(() => {
      this.getData();
    });

    this.getData();
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  //************************************************************************************************ */
  reqStatuses: any[] = [
    { name: "ALL" },
    { name: "PENDING" },
    { name: "APPROVED" },
    { name: "RETURNED" },
    { name: "REJECTED" },
  ];
  selectedStatus: string = "ALL";

  getSelectSelectedStatus() {
    // this.getData();
  }
  getSelectedStatus(status: any) {
    this.Form.patchValue({
      status: status,
    });

    //  this.getData();
  }
  getData() {
    this.loading = true;
    this.dataSource = new MatTableDataSource([]);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    this.selectedStatus = this.Form.value.status;

    let params = {
      costcentercode: this.Form.value.costCenterCode,
      Status: this.Form.value.status,
      year: this.Form.value.year,
    };

    this.leaveRequestService
      .getEmployeesLeavesByCostCenter(params)

      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          if (res.statusCode === 302) {
            this.data = res.entity;
            console.log("Leave Requests displayed on table", this.data);

            this.loading = false;
            this.dataSource = new MatTableDataSource(this.data);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          } else {
            this.loading = false;
          }
        },
        error: (err) => {
          this.loading = false;
          this.notificationAPI.alertWarning("Server Error:  " + err.message);
        },
        complete: () => { },
      });
  }
  refresh() {
    this.getData();
  }

  mngRecord(action: any, record?: any) {
    console.log("this.action:; ", action);
    console.log("this.record:; ", record);

    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: action,
      record: record,
    };
    const dialogRef = this.dialog.open(
      ManageLeaveRequestsComponent,
      dialogConfig
    );
    dialogRef.afterClosed().subscribe((result) => {
      console.log("result:::", result);

      this.getData();
    });
  }
  deleteRecord(id: number) {
    Swal.fire({
      title: "Are you sure?",
      text: "HOLIDAY RECORD WILL BE DELETED PERMANENTLY!!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        this.loading = true;
        this.leaveRequestService
          .delete(id)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              if (res.statusCode == 200) {
                this.getData();
                this.loading = false;
                this.notificationAPI.alertSuccess(
                  "HOLIDAY RECORD DELETED SUCCESSFULLY"
                );
              } else {
                this.getData();
                this.loading = false;
                this.notificationAPI.alertWarning(res.message);
              }
            },
            error: (err) => {
              this.loading = false;
              this.notificationAPI.alertSuccess("Server Error: " + err.message);
            },
            complete: () => { },
          });
      }
    });
  }

  input: any;
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.input = filterValue;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
  //******************************************************************************************************************************** */
  onCostCenterLookup(type: any) {
    if (type === "Search") {
      const dialogConfig = new MatDialogConfig();
      dialogConfig.disableClose = false;
      dialogConfig.disableClose = true;
      dialogConfig.width = "800px";
      dialogConfig.data = {
        action: "SingleCostCenter",
      };
      const dialogRef = this.dialog.open(CcentreLookupComponent, dialogConfig);
      dialogRef.afterClosed().subscribe((res) => {
        if (res && res.data && res.data.length > 0) {
          this.Form.patchValue({
            costCenterCode: res.data[0].costCenterCode,
          });
        }
      });
    } else if (type === "All") {
      this.Form.patchValue({
        costCenterCode: "All",
      });

      this.getData();
    }
  }
  // ****************************************************************************************************************************
  selectedRows: any[] = [];
  atleastOneSelected: boolean = false;
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }
  masterToggle() {
    if (this.isAllSelected()) {
      this.selection.clear();
      return;
    }

    this.selection.select(...this.dataSource.data);
  }
  checkboxLabel(row?: any): string {
    if (!row) {
      return `${this.isAllSelected() ? "deselect" : "select"} all`;
    }
    return `${this.selection.isSelected(row) ? "deselect" : "select"} row ${row.position + 1
      }`;
  }

  expSelected() {
    this.atleastOneSelected = true;
    this.selectedRows = this.selection.selected;

    console.log("this.selectedRows: ", this.selectedRows);
  }

  hasAccess: boolean = true;
  processMultipleRows(status: any) {
    let privilege = "Validate Expenses";

    // this.hasAccess = this.accessControlService.hasPrivilege("FinanceModule", [
    //   privilege,
    // ]);

    if (this.hasAccess) {

      Swal.fire({
        title: '<span style="font-size: 20px;">Enter Details Below: </span>',
        html:
          '<select id="approverSelect" class="swal2-select">' +
          '<option value="" disabled selected>Select Approver</option>' +
          '<option value="Supervisor Leave Approver">Supervisor Leave Approver</option>' +
          '<option value="Human Resource Leave Approver">Human Resource Leave Approver</option>' +
          '</select>' +
          '<textarea id="remarks" class="swal2-textarea" placeholder="Type your remarks here..." style="margin-top: 15px;"></textarea>',
        icon: "info",
        confirmButtonText: "Submit",
        showCancelButton: true,
        cancelButtonText: "Cancel",
        preConfirm: () => {
          const approverSelect = Swal.getPopup().querySelector('#approverSelect') as HTMLSelectElement;
          const approver = approverSelect.value;

          const remarksTextarea = Swal.getPopup().querySelector('#remarks') as HTMLTextAreaElement;
          const verifierRemarks = remarksTextarea.value;

          if (!approver) {
            Swal.showValidationMessage('Please select an approver');
          }
          if (!verifierRemarks) {
            Swal.showValidationMessage('Please enter your remarks');
          }

          return { approver, verifierRemarks };
        }
      }).then((result) => {
        if (result.isConfirmed) {
          const { approver, verifierRemarks } = result.value;


          const processedRows = this.selectedRows.map((element) => {
            return {
              id: element.id,
              status: status,
              verifierRemarks: verifierRemarks,
            };
          });

          let params = { approver: approver }
          this.leaveRequestService
            .updateState(params, processedRows)
            .pipe(takeUntil(this.destroy$))
            .subscribe({
              next: (res) => {
                console.log("res: ", res);
                if (res.statusCode == 200) {
                  this.snackbar.showNotification("snackbar-success", res.message);
                  this.selection = new SelectionModel<any>(true, []);
                  this.selection.clear();
                } else {
                  this.snackbar.showNotification("snackbar-danger", res.message);
                }
              },
              error: (err) => {
                this.snackbar.showNotification("snackbar-danger", err.message);
                this.selection = new SelectionModel<any>(true, []);
                this.selection.clear();
              },
              complete: () => {
                this.getData();
              },
            });
        } else {
          this.selection = new SelectionModel<any>(true, []);
          this.selection.clear();
        }
      });

    }
  }
}
